            margin-right: -24px;
            margin-left: -34%;
          ">
        <span class="mask bg-gradient-success opacity-6"></span>
      </div>
      <div class="card shadow-lg mt-n6">
        <div class="card-body p-3">
          <div class="row gx-4">
            <div class="col-auto">
              <div class="avatar avatar-xl position-relative">
                <img src="@/assets/img/sawad-logo.png" alt="profile_image" class="shadow-sm w-100 border-radius-lg" />
              </div>
            </div>
            <div class="col-auto my-auto">
              <div class="h-100">
                <h5 class="mb-1">{{ userData.name }}</h5>
                <p class="mb-0 font-weight-bold text-sm">{{ userData.email }}</p>
              </div>
            </div>
            
          </div>
        </div>
      </div>
    </div>
    <div class="py-4 container-fluid">
      <div class="row">
        <div class="col-md-8">
          <div class="card">
            <div class="card-header pb-0">
              <div class="d-flex align-items-center">
                <p class="mb-0">Edit Profile</p>
                <argon-button color="success" size="sm" class="ms-auto">Settings</argon-button>
              </div>
            </div>
            <div class="card-body">
              <p class="text-uppercase text-sm">User Information</p>
              <div class="row">
                <div class="col-md-6">
                  <label for="example-text-input" class="form-control-label">Full name</label>
                  <argon-input type="text" :value="userData.name" />
                </div>
                <div class="col-md-6">
                  <label for="example-text-input" class="form-control-label">Email address</label>
                  <argon-input type="email" :value="userData.email" />
                </div>

                <div class="col-md-6">
                  <label for="example-text-input" class="form-control-label">Phone Number</label>
                  <argon-input type="email" :value="userData.phone_number" />
                </div>

                <div class="col-md-6">
                  <label for="example-text-input" class="form-control-label">Join date</label>
