<template>
  <div class="container top-0 position-sticky z-index-sticky">
    <div class="row">
      <div class="col-12">
        <navbar
          isBlur="blur  border-radius-lg my-3 py-2 start-0 end-0 mx-4 shadow"
          v-bind:darkMode="true"
          isBtn="bg-gradient-success"
        />
      </div>
    </div>
  </div>
  <main class="mt-0 main-content">
    <section>
      <div class="page-header min-vh-100">
        <div class="container">
          <div class="row">
            <div class="mx-auto col-xl-4 col-lg-5 col-md-7 d-flex flex-column mx-lg-0">
              <div class="card card-plain">
                <div class="pb-0 card-header text-start">
                  <h4 class="font-weight-bolder">Sign In</h4>
                  <p class="mb-0">Enter your email and password to sign in</p>
                </div>
                <div class="card-body">
                  <form role="form" @submit.prevent="onLoginSubmit">
                    <div class="mb-3">
                      <argon-input v-model:value="emailModel" type="email" placeholder="Email" name="email" size="lg" />
                      
                    </div>
                    <div class="mb-3">
                      <argon-input v-model:value="passwordModel" type="password" placeholder="Password" name="password" size="lg" />
                    </div>
                    <argon-switch id="rememberMe">Remember me</argon-switch>

                    <div class="text-center">
                      <argon-button
                        class="mt-4"
                        variant="gradient"
                        color="success"
                        fullWidth
                        size="lg"
                      >Sign in</argon-button>
                    </div>
                  </form>
                </div>
                <div class="px-1 pt-0 text-center card-footer px-lg-2">
                  <p class="mx-auto mb-4 text-sm">
                    Don't have an account?
                    <a
                      href="javascript:;"
                      class="text-success text-gradient font-weight-bold"
                    >Sign up</a>
                  </p>
                </div>
              </div>
            </div>
            <div
              class="top-0 my-auto text-center col-6 d-lg-flex d-none h-100 pe-0 position-absolute end-0 justify-content-center flex-column"
            >
              <div
                class="position-relative bg-gradient-primary h-100 m-3 px-7 border-radius-lg d-flex flex-column justify-content-center overflow-hidden"
                :style="'background-image: url('+ require('@/assets/img/signin-ss.jpg')+');background-size: cover;'"
              >
                <span class="mask bg-gradient-success opacity-6"></span>
                <h4
                  class="mt-5 text-white font-weight-bolder position-relative"
                >Sign In with your credentials given</h4>
                <p
                  class="text-white position-relative"
                >Use your email and password </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import ArgonInput from "@/components/ArgonInput.vue";
import ArgonSwitch from "@/components/ArgonSwitch.vue";
import ArgonButton from "@/components/ArgonButton.vue";
import HTTP from "../api/axios.ts";
import swal from 'sweetalert';

const body = document.getElementsByTagName("body")[0];

export default {
  name: "signin",

  data(){
    return {
      emailModel: "",
      passwordModel:""
    }
  },
  components: {
    ArgonInput,
    ArgonSwitch,
    ArgonButton,
  },
  created() {
    this.$store.state.hideConfigButton = true;
    this.$store.state.showNavbar = false;
    this.$store.state.showSidenav = false;
    this.$store.state.showFooter = false;
    body.classList.remove("bg-gray-100");
  },
  beforeUnmount() {
    this.$store.state.hideConfigButton = false;
    this.$store.state.showNavbar = true;
    this.$store.state.showSidenav = true;
    this.$store.state.showFooter = true;
    body.classList.add("bg-gray-100");
  },


  methods:{
    
    onLoginSubmit(){
     console.log(this.passwordModel);
      HTTP.post("/client/auth/login",{email: this.emailModel,password: this.passwordModel}).then(function(dataRes){

        console.log(dataRes.data);

        let loginData = dataRes.data.data;
        let accessToken = loginData.accessToken;

        localStorage.setItem("accessToken",accessToken);

        window.location.href = "/";


      }).catch(function(error){
       
          if(error.response.status === 500){
            swal({
              title: "Wrong credentials",
              text: "Email or password incorrect!",
              icon: "error",
              dangerMode: true,
            });
          }else{
            swal({
              title: "something went wrong",
              text: error.response.data.message,
              icon: "error",
              dangerMode: true,
            });

          }
      });
    }
  }
};
</script>
